import React, { Component } from 'react';
import './App.css';
import Teacher from './Teacher'
import QuanLyDiem from './QuanlyDiem'
import Monhoc from './QuanLyMonHoc'
import MonHoc from './QuanLyMonHoc';
import Diem from './QuanlyDiem';
class Menu extends Component {
    constructor(props){
        super(props);
        this.state = {
            isDisplaySideBar : false,
            isShow : -1 // -1 : Điểm , 0 : môn học , 1 : Giáo viên
        }
    }
    isShowDiem = () => {
        this.setState({
            isShow : -1
        })
   }
    
    isShowMonHoc = () => {
         this.setState({
             isShow : 0
         })
    }

    isShowTeacher = () => {
        this.setState({
            isShow : 1
        })
   }

    onBar = () => {
       this.setState({
           isDisplaySideBar : !this.state.isDisplaySideBar
       })
    }
    
    onCancel = () => {
        this.setState({
            isDisplaySideBar : !this.state.isDisplaySideBar
        })
    }

        render(){
            var {isShow} = this.state
           
        return (
            <div>

            
                            <input type='checkbox' id='check'/>
                        <label htmlFor="check">
                            <i className= "fas fa-bars" id= "btn" onClick = {this.onBar}></i>
                            <i className="fas fa-times" id="cancel" onClick = {this.onCancel} ></i>
                        </label>
                        <div className='sidebar'>
                            <header>Menu</header>
                            <ul>
                                <li><a>Trang chủ</a></li>
                                <li><a onClick = {this.isShowTeacher}
                                >Quản lý giáo viên</a></li>
                                <li><a onClick = {this.isShowDiem}
                                >Quản lý điểm</a></li>
                                <li><a onClick = {this.isShowMonHoc}
                                 >Quản lý môn học</a></li>
                            </ul>
                        </div> 

                             {/* if({this.state.isShow === 1}){
                                 <Teacher />
                             }else if ({this.state.isShow === 0}) {
                                 <MonHoc />
                             }else {
                                 <Diem />
                             } */}
                        
                       {this.state.isShow === 0 ? <MonHoc /> : <Diem />}
            </div>       
                
            
        );
    }
}



export default Menu;