import React, { Component } from 'react';
import './App.css';
import TaskFormMonHoc from './components/TaskFormMonHoc'
import TaskListMonHoc from './components/TaskListMonHoc'
import Menu from './menu'
class MonHoc extends Component {
    constructor(props){
        super(props);
        this.state = {
            tasksS :[],
            isDisplaySideBar : false,
            
        }
    }
    

    onCloseForm = () =>{
        this.setState({
            isDisplayForm : !this.state.isDisplayForm
        })
    }
   
    /* LocalStorage  */
    componentDidMount() {
        if(localStorage && localStorage.getItem('tasksS')){
            var tasksS = JSON.parse(localStorage.getItem('tasksS'));
            this.setState({
                tasksS: tasksS
            });
        }
    }

   onReset = () => {
       var tasksS = [
           
       ]
       this.setState({
           tasksS : tasksS
       })
       localStorage.setItem('tasksS', JSON.stringify(tasksS));
   }
    
    

    s4() {
        return Math.floor(1+Math.random() * 0x10000).toString(16).substring(1);
    }  

    generateID() {
        return this.s4() + this.s4() + '-' + this.s4() + '-' + this.s4() + '-' + this.s4() + '-' 
              + this.s4() + this.s4() + this.s4();
    }
    

    onSubmit = (data) => {
        console.log(data);
        var {tasksS } = this.state;
        data.id = this.generateID();
        if(data.hoten !==''){
            tasksS.push(data);
        }
        this.setState({
            tasksS : tasksS
        })
        localStorage.setItem('tasksS', JSON.stringify(tasksS));
    }

    


    findIndex = (id) => {
        var { tasksS } = this.state;
        var result = -1;
        tasksS.forEach((task,index) => {
            if(task.id === id){
                
                result = index;
            }
         });
         return result;
    }

    onDeleteMonHoc = (id) => {
        var {tasksS} = this.state;
        var index = this.findIndex(id);
        if(index !== -1 ){
            tasksS.splice(index, 1);
            this.setState({
                tasksS : tasksS
            });
            localStorage.setItem('tasksS', JSON.stringify(tasksS));
        }
        this.onCloseForm();
    }
        render(){

            var  { isDisplaySideBar , isDisplayForm ,tasksS } = this.state;
            
            // onCloseForm = {this.onCloseForm}
            // onSubmit = {this.onSubmit}
        // /> 
            // : ""
        return (

                    <div className='Form-app-monhoc'>
                            <div className="abc">
                                <div className="row">
                                <h1 className='text-center'>Quản lý môn học</h1>
                                    <div className="col-xs-12 col-sm-5 col-md-4 col-lg-4 mt-50">
                                            { /* Form Add Teacher */ }
                                        <TaskFormMonHoc onCloseForm = {this.onCloseForm}
                                                        onSubmit = {this.onSubmit}
                                                        onClearAdd ={this.onClearAdd}/>
                                        
                                    </div>
                                    
                                    <div className=  "col-xs-12 col-sm-8 col-md-8 col-lg-8 mt-50" 
                                                
                                        //  className = {isDisplaySideBar === false 
                                        //         ? "col-xs-8 col-sm-8 col-md-8 col-lg-8 abcd mt-50" 
                                        //         : "col-xs-10 col-sm-10 col-md-10 col-lg-10 abcd mt-50"}
                                         >
                                            
                                            
                                            <div className="panel panel-primary panel-class">
                                                  <div className="panel-heading">
                                                        <h3 className="panel-title">Danh sách môn học</h3>
                                                  </div>
                                                  <div className="panel-body">
                                                        {/* Task List*/}
                                            <TaskListMonHoc  tasksS = {tasksS}
                                                           
                                                           onDeleteMonHoc = {this.onDeleteMonHoc}/>
                                                  </div>
                                            </div>
                                    </div>
                                </div>

                            </div>
                       </div>

                
                
            
        );
    }
}



export default MonHoc;